import React from 'react';
import _ from 'lodash';
import { validation } from '../../containers/Attributes/Forms';

export const SimpleSelect = ({emptyOptionText, emptyOptionValue, optionText, optionValue, data, onChangeHandler, ...rest}) => {
    emptyOptionText = !_.isUndefined(emptyOptionText) ? emptyOptionText : "Select Parent Clipart";
    emptyOptionValue = !_.isUndefined(emptyOptionValue) ? emptyOptionValue : '';
    return <div className="promize-form-group"> 
                <label className="promize-form-control-label">{rest.labelName && rest.labelName}{rest.validation ? <sup>*</sup> : ''}</label>
                <select className="promize-form-control"  {...rest} onChange={onChangeHandler}>
                        <option value={emptyOptionValue}>{emptyOptionText}</option>
                        {
                            data && data.map((option) => {
                                return <option id={`simple-select-${option[optionValue]}`} key={`simple-select-${option[optionValue]}`} value={option[optionValue]}>{option[optionText]}</option>
                            })
                        }
                </select>
        </div>
}