import React, { Component } from 'react';
import { connect } from 'react-redux';
import {app} from '../../config/app';
import noImages from '../../assets/images/no-image.png';
import Dropzone from 'react-dropzone'

class ImageUpload extends Component {

    constructor(props) {
        super(props);
        this.state = {
            highlight: false
        }
        this.setImageKey = this.setImageKey.bind(this);
        this.setDate = this.setDate.bind(this);
        this.change = this.change.bind(this);
        this.onDrop = this.onDrop.bind(this);
        this.onDragOver = this.onDragOver.bind(this);
        this.onDragLeave = this.onDragLeave.bind(this);
    }
    /**
     * Set Data Callback
     * Todo 
     *  - remove this funcition
     */
    setImageKey(){
        if(this.props.callBack == true && typeof this.props.setImageKey == "function"){
            this.props.setImageKey(this.props.imageType)
        }
    }
    /**
     * Set Data Callback
     */
    setDate(){
        if(this.props.callBack == true && typeof this.props.setDate == "function"){
            this.props.setDate(this.props.data)
        }
    }

    change(e){
        this.props.form.handleFileSelect(e);
        e.target.value = null;  
        this.setImageKey() // Todo: Remove this function call
        this.setDate()      
    }
    
    onDrop(acceptedFiles){
        this.props.form.handleFileDrop({"files":acceptedFiles},"image_file")
        this.setImageKey() // Todo: Remove this function call
        this.setDate()
        this.setState({highlight:false})
    }

    onDragOver(e){
        this.setState({highlight:true})
    }

    onDragLeave(e){
        this.setState({highlight:false})
    }

    render() {
        let {highlight} = this.state
        return (<Dropzone onDrop={this.onDrop} onDragOver={this.onDragOver} onDragLeave={this.onDragLeave}>
                {({getRootProps, getInputProps}) => (
                    <div className={this.props.type == 'icon' ? "promize-upload promize-upload-sm" :"promize-upload"} {...getRootProps()}>
                        <div className="promize-upload-header">
                            <label className="promize-upload-title">{this.props.labelName}</label>
                            <div className="custom-upload-product">
                                <div className="promize-upload-button">
                                    <input {...getInputProps()} type="file" name="image_file" id="image_file" onChange={this.change}/>
                                    {/* <span className="file-custom">Upload Image</span> */}
                                </div>
                            </div>
                        </div>
                        <div className={"promize-upload-content " + (highlight?'promize-highlight':'')}>
                            {this.props.type == 'icon' && <>
                            {!this.props.image_url &&  <div className="promize-content-before-upload">
                                {!this.props.image_url &&  <><i class="fa fa-upload"></i><p>Browse</p></> } 
                            </div>}
                            {this.props.image_url && <div className="promize-content-after-upload">
                                {this.props.image_url && <img src={app.APP_IMAGE_BASE_URL + this.props.image_url} />}
                            </div>}
                            {this.props.image_url && <button class="btn promize-upload-edit">
                                <i class="fa fa-pencil-alt"></i>
                            </button>}
                            </>
                            }
                            {!this.props.type &&  <div  className="promize-content-before-upload">
                            {!this.props.image_url && <img src={noImages} /> }
                            {this.props.image_url && <img src={app.APP_IMAGE_BASE_URL + this.props.image_url} />}
                            </div>}
                        </div>
                    </div>
                    )}
                </Dropzone>
                );
    }
}

function mapStateToProps(state) {
    const { image } = state;
    return {
        image
    };
}

export default connect(mapStateToProps)(ImageUpload);
