import React from 'react';
import Select from 'react-select';
const CustomOption = function({data,innerRef,innerProps}){
    return <div ref={innerRef} {...innerProps}  className="multi_select_option">
        <span className="multi_select_label">{data.topLabel}</span>
         <span className="multi_select_sub_label"> {data.subLabel}</span>
    </div>
}
class MultiSelect extends React.Component {
    render() {
        return (<div className="promize-form-group">
            <label className="promize-form-control-label">{this.props.labelName}  {this.props.validation ? <sup>*</sup> : ''}</label>
            <Select
                closeMenuOnSelect={false}
                value={this.props.value}
                onChange={this.props.onChange}
                options= {this.props.options}
                components={this.props.subLabel && { Option: CustomOption }}         
                isMulti = {this.props.isMulti}
            />
        </div>);
    }
}

export default MultiSelect;
