import React, { Component } from 'react';
import { connect } from 'react-redux';
import _ from 'lodash';
import { getAllCliparts } from '../../../actions/cliparts';
import Input from '../../../components/form/input';
import MultiSelect from '../../../components/form/MultiSelect';

class Clipart extends Component {
    constructor(props){
        super(props)
        this.state ={
            promize_attribute_clipart_values : {
                "clipart_name" : '',
                "clipart_price" : null,
                "clipart_sku" : null,
                "promize_domain_clipart_category_id" : '',
                "promize_attribute_id" : this.props.state.fields.promize_attribute_id,
            },
            clipartList : [],
            selectedClipartList : []
        }
        this.handleClipartFieldChange = this.handleClipartFieldChange.bind(this);
        this.handleClipartsSelect = this.handleClipartsSelect.bind(this);    
    }

    componentWillMount(){
        this.props.dispatch(getAllCliparts());
    }

    componentWillReceiveProps(props){
        if(!props.cliparts.isFetching && !props.cliparts.isError  && this.props.cliparts.isFetching !== props.cliparts.isFetching){
            let cliparts = props.cliparts.response.data, lists=[];
            cliparts.map((clipart, index)=>{
                let clipartList = {};
                clipartList.label = clipart.clipart_category_name;
                clipartList.value = clipart.promize_domain_clipart_category_id;
                lists.push(clipartList);
            })
            this.setState({clipartList : lists},()=>{
                if(props.state.fields.promize_attribute_clipart_values && lists.length > 0){
                    let {promize_domain_clipart_category_id} = props.state.fields.promize_attribute_clipart_values;
                    let selectedClipartList = [], clipartList = this.state.clipartList
                    let clipIdArray = promize_domain_clipart_category_id ? promize_domain_clipart_category_id.split(',') : [];
                    clipIdArray.length > 0 && clipartList && clipIdArray.map((clipId,index)=>{
                        let clips = _.find(clipartList, ['value', parseInt(clipId)]);
                        clips && selectedClipartList.push(clips)
                    })
                    this.setState({selectedClipartList})
                }
            });
        } 
    }

    handleClipartFieldChange(e, fieldName){
        e.preventDefault();
        let fields = this.props.state.fields;
        let promize_attribute_clipart_values = fields.promize_attribute_clipart_values;
        promize_attribute_clipart_values[fieldName]  = e.target.value;
        const newObj = Object.assign({}, fields, {          
            promize_attribute_clipart_values: promize_attribute_clipart_values
        });
        this.props.currentComponent.setState({fields: newObj});
    }

    handleClipartsSelect(value){
        let ids = []
        value && value.map((data, index)=>{
            ids.push(data.value)
        })
        let selected_ids = ids.length > 0 ? ids.join(',') : null;
        let fields = this.props.state.fields;
        let promize_attribute_clipart_values = fields.promize_attribute_clipart_values;
        promize_attribute_clipart_values['promize_domain_clipart_category_id']  = selected_ids;
        promize_attribute_clipart_values['promize_attribute_id']  = this.props.state.fields.promize_attribute_id;
        const newObj = Object.assign({}, fields, {          
            promize_attribute_clipart_values: promize_attribute_clipart_values
        });
        this.props.currentComponent.setState({fields: newObj});
        this.setState({ selectedClipartList : value})    
    }

    render(){
        return <React.Fragment>
        <div className="promize-card-header">
          <div className="promize-card-title">
              <h3>
                  <span>Cliparts</span>
              </h3>
          </div>
      </div>
      <div className="promize-card-body">
      <div className="df">
          <div className="wd-33 mr-15">
              <Input {...this.props}
                  fieldName = {'clipart_name'}
                  value={this.props.state.fields.promize_attribute_clipart_values.clipart_name}
                  labelName={"Clipart Name"}
                  validation={true}
                  onChange={(e) => { this.handleClipartFieldChange(e, "clipart_name") }}
              />    
          </div>
     
          <div className="wd-33 mr-15">
              <Input {...this.props}
                  fieldName = {'clipart_price'}
                  value = { this.props.state.fields.promize_attribute_clipart_values.clipart_price}
                  labelName={"Clipart Price"}
                  validation={false}
                  inputType={'number'}
                  onChange={(e) => { this.handleClipartFieldChange(e, "clipart_price") }}
              />    
          </div>
     
          <div className="wd-33 mr-15">
              <Input {...this.props}
                  fieldName = {'clipart_sku'}
                  value={this.props.state.fields.promize_attribute_clipart_values.clipart_sku}
                  labelName={"Clipart SKU"}
                  validation={false}
                  onChange={(e) => { this.handleClipartFieldChange(e, "clipart_sku") }}
              />    
          </div>
            <div className="wd-33">
            <MultiSelect
                labelName = {"Select cliparts"}
                selectedOption = { this.props.state.fields.promize_attribute_clipart_values.promize_domain_clipart_category_id} 
                onChange={this.handleClipartsSelect}
                options= {this.state.clipartList}
                value = {this.state.selectedClipartList}
                validation={true}
                isMulti = {true}
            />
      </div>
      </div>
      </div>
  </React.Fragment>
    }
}
function mapStateToProps(state) {
    return {
        attributeValues : state.attributeValues,
        cliparts : state.cliparts,
    };
}
export default connect(mapStateToProps)(Clipart);