import { FONT_SIZE,FONT_SIZES,PENDING, FULFILLED, REJECTED } from "../../utility/constants";
import { app } from "../../config/app";

export function save(data) {
    let method = 'POST', url = "createRecord";
    data = {
        ...data,
        font_size : parseInt(data.font_size),
        modelName: "promize_font_size",
        idKey: "font_size_id"
    }
    if (data.font_size_id) {
        method = 'PUT';
        url = 'updateRecord'
        delete data.created_date;
        delete data.updated_date;
    }
    return {
        types: [`${FONT_SIZE}_${PENDING}`, `${FONT_SIZE}_${FULFILLED}`, `${FONT_SIZE}_${REJECTED}`],
        method: method,
        url: url,
        data: data,
        payload: { receiveType: 'postData' }
    }
}

export function getAllFontSizes() {
    let data = {
        modelName: "promize_font_size",
        idKey: "font_size_id",
        domain_id: app.DOMAIN_ID
    }
    const request = {
        types: [`${FONT_SIZES}_${PENDING}`, `${FONT_SIZES}_${FULFILLED}`, `${FONT_SIZES}_${REJECTED}`],
        method: 'POST',
        url: 'getAllRecords',
        data: data,
    }

    return request;
}
