import { ATTRIBUTE, ATTRIBUTES, ATTRIBUTE_VALUES, ATTRIBUTE_VALUES_DELETE, ATTRIBUTE_VALUES_IMAGES, ATTRIBUTE_VALUES_IMAGES_DELETE, IMAGE_UPLOAD, TEXT_VALUES, TEXT_VALUES_DELETE,ATTRIBUTE_CLIPART_VALUES ,PENDING, FULFILLED, REJECTED, ATTRIBUTE_UPLOAD_IMAGE_VALUES } from "../../utility/constants";
import { app } from "../../config/app";

export function save(data){
    let method = 'POST', url='createRecord';
    data = {
        ...data,
        status : data.status ? 1 : 0,
        modelName:"promize_attribute",
        idKey:"promize_attribute_id"
    }
	if(data.promize_attribute_id){
        method = 'PUT';
        url= 'updateRecord';
        delete data.created_date;
        delete data.updated_date;
	}
    return {
        types: [`${ATTRIBUTE}_${PENDING}`, `${ATTRIBUTE}_${FULFILLED}`, `${ATTRIBUTE}_${REJECTED}`],
        method: method,
        url: url,
        data: data,
        payload: {receiveType:'postData'}
    }
}
