import React, { Component } from 'react';
import { connect } from 'react-redux';
import $ from 'jquery';
import { NavLink } from 'react-router-dom';
import _ from 'lodash';
import toastr from 'toastr';
import Input from '../../components/form/input';
import TypeSelection from '../../components/form/TypeSelection';
import { SubmitButton } from "../../components/form/SubmitButton";
import { save, saveAttributeValues, saveAttributeClipartValues, saveAttributeValuesImage, deleteAttributeValuesImage, deleteAttributeValues, saveAttributeUploadImageValues, saveTextValues } from '../../actions/attributes';
import { app } from '../../config/app';
import ImageUpload from '../../components/files/ImageUpload';
import ImageUploadMultilple from '../../components/files/ImageUploadMultilple';
import MultiSelect from '../../components/form/MultiSelect';
import { getAllCliparts } from '../../actions/cliparts';
import Text from './Type/text';
import Clipart from './Type/clipart';
import Image from './Type/image'
import Switchtoggle from '../../components/form/switchtoggle';
import Popup from '../../components/design/Popup'

export function initialState() {
    const initialState = {
        fields:
        {
            "domain_id": app.DOMAIN_ID,
            "promize_attribute_id": null,
            "attribute_name": "",
            "attribute_code": "",
            "attribute_type": "option",
            "promize_attribute_values": [],
            "promize_attribute_clipart_values": {},
            "promize_attribute_image_values": {},
            "promize_attribute_text_values": {},
            "status": true,
        },
        inputErrors: {},
        isEdit: true,
        attribute_saved: false
    };

    return initialState;
}

export const validation = (props) => {
    const rules = {
        "attribute_name": "required",
    }
    return rules;
}
class Forms extends Component {
    constructor(props) {
        super(props);
        this.state = {
            popup: false,
            promize_attribute_values: {
                "promize_attribute_id": this.props.state.fields.promize_attribute_id,
                "promize_attribute_values_id": null,
                "option_name": "",
                "promize_images": [],
                "option_thumbnail_image": {
                    "image_type": '',
                    "image_url": '',
                    "promize_attribute_values_id": null,
                    "promize_images_id": null
                },
                "option_code": "",
                "option_value": "",
                "option_sku": "",
                "option_price": null
            },
            promize_images: {
                "image_type": '',
                "image_url": '',
                "promize_attribute_values_id": null,
                "promize_images_id": null
            },
            imageKey: '',
            enableEditAttribute: false,
        }

        this.handleFieldChange = this.handleFieldChange.bind(this);
        this.setImageKey = this.setImageKey.bind(this);
        this.handleAddOption = this.handleAddOption.bind(this);
        this.setWrapperRef = this.setWrapperRef.bind(this);
        this.handleClickOutside = this.handleClickOutside.bind(this);
        this.deleteImage = this.deleteImage.bind(this);
        this.showOptionContentPopup = this.showOptionContentPopup.bind(this);
        this.handleSaveAttributeValuesImage = this.handleSaveAttributeValuesImage.bind(this);
        this.handleInputFieldBlur = this.handleInputFieldBlur.bind(this);
        this.saveForm = this.saveForm.bind(this);
    }

    handleUploadedImage(props) {
        const response = props.imageUpload.response;
        if (response.status && response.status === 200) {
            let promize_attribute_values = this.state.promize_attribute_values;
            if (this.state.imageKey == "option_image") {
                let saveData = {
                    "image_type": 'option_image',
                    "image_url": response.file.path,
                    "promize_attribute_values_id": promize_attribute_values.promize_attribute_values_id,
                    "promize_images_id": null,
                    "status": 1,
                }
                this.props.dispatch(saveAttributeValuesImage(saveData));
            } else {
                let saveData = {
                    "image_type": 'option_thubnail_image',
                    "image_url": response.file.path,
                    "promize_attribute_values_id": promize_attribute_values.promize_attribute_values_id,
                    "promize_images_id": null,
                    "status": 1,
                }
                this.props.dispatch(saveAttributeValuesImage(saveData));
            }
        }
    }
    handleSaveAttributeValuesImage(props) {
        let records = props.attributeValuesImages.response.records;
        let attributeValues;
        let promize_attribute_values = this.state.promize_attribute_values;
        if (this.state.imageKey == "option_image") {
            let promizeImages = this.state.promize_attribute_values.promize_images;
            promizeImages.push(records);
            attributeValues = Object.assign({}, promize_attribute_values, {
                promize_images: promizeImages
            });
        } else {
            attributeValues = Object.assign({}, promize_attribute_values, {
                option_thumbnail_image: records
            });
        }
        this.setState({ promize_attribute_values: attributeValues });
    }

    componentWillReceiveProps(props) {
        if (props.imageUpload.response !== this.props.imageUpload.response) {
            this.handleUploadedImage(props);
        } else if (!props.attributeValuesImages.isFetching && !props.attributeValuesImages.isError && this.props.attributeValuesImages.isFetching !== props.attributeValuesImages.isFetching) {
            this.handleSaveAttributeValuesImage(props);
        }
        else if (!props.attributeValues.isFetching && !props.attributeValues.isError && this.props.attributeValues.isFetching !== props.attributeValues.isFetching) {
            let records = props.attributeValues.response.records;
            records.promize_images = this.state.promize_attribute_values.promize_images;
            records.option_thumbnail_image = this.state.promize_attribute_values.option_thumbnail_image;
            this.setState({ promize_attribute_values: records }, () => {
                //    $('.promize-modal-cover').show();  
            });
        }

    }
    handleFieldChange(e, fieldName) {
        e.preventDefault();
        let fields = this.state.promize_attribute_values;
        let obj = fields
        obj[fieldName] = e.target.value;
        const newObj = Object.assign({}, fields, obj);
        fields = newObj;
        this.setState({ promize_attribute_values: fields });
    }

    showOptionContentPopup(e, data, index) {
        e.preventDefault();
        if (index !== undefined) {
            const promize_attribute_values = this.state.promize_attribute_values;
            data.arrayIndex = index;
            const newObj = Object.assign({}, promize_attribute_values, data);
            this.setState({ promize_attribute_values: newObj });
        } else {
            let promize_attribute_values = this.state.promize_attribute_values;
            promize_attribute_values.promize_attribute_id = this.props.state.fields.promize_attribute_id
            this.props.dispatch(saveAttributeValues(promize_attribute_values));
            $('.promize-modal-cover').show();
        }
    }
    hideOptionContentPopup(e) {
        e && e.preventDefault();
        if (!this.state.promize_attribute_values.option_name) {
            let promize_attribute_values_id = this.state.promize_attribute_values.promize_attribute_values_id
            let fields = this.props.state.fields;
            let promize_attribute_values = fields.promize_attribute_values;
            let selected_index = -1;
            promize_attribute_values.map((value, attr_index) => {
                if (value.promize_attribute_values_id == promize_attribute_values_id) {
                    selected_index = attr_index
                }
            });
            (selected_index !== -1) && promize_attribute_values.splice(selected_index, 1);
            const newObj = Object.assign({}, fields, {
                promize_attribute_values: promize_attribute_values
            });
            this.props.currentComponent.setState({ fields: newObj }, () => {
                this.props.dispatch(deleteAttributeValues({ "ids": [promize_attribute_values_id] }))
            });
        }
        $('.promize-modal-cover').hide();
        const newObj = {
            "promize_attribute_id": this.props.state.fields.promize_attribute_id,
            "promize_attribute_values_id": null,
            "option_name": "",
            "promize_images": [],
            "option_thumbnail_image": {
                "image_type": '',
                "image_url": '',
                "promize_attribute_values_id": null,
                "promize_images_id": null
            },
            "option_code": "",
            "option_value": "",
            "option_sku": "",
            "option_price": null
        }
        this.setState({ promize_attribute_values: newObj });
    }
    handleAddOption(e) {
        e.preventDefault();
        let fields = this.props.state.fields;
        let promize_attribute_values = fields.promize_attribute_values;
        if (this.state.promize_attribute_values.option_name) {
            if (this.state.enableEditAttribute) {
                var foundIndex = promize_attribute_values.findIndex(attr_obj => attr_obj.promize_attribute_values_id == this.state.promize_attribute_values.promize_attribute_values_id);
                promize_attribute_values[foundIndex] = this.state.promize_attribute_values;
            } else {
                promize_attribute_values.push(this.state.promize_attribute_values);
            }
            const newObj = Object.assign({}, fields, {
                promize_attribute_values: promize_attribute_values
            });
            this.setState({ enableEditAttribute: false });
            this.hideOptionContentPopup();
            this.props.currentComponent.setState({ fields: newObj });
        } else if (!this.state.promize_attribute_values.option_name) {
            toastr.error("Option name cannot be empty")
        }

    }
    setImageKey(key) {
        this.setState({ imageKey: key });
    }
    deleteImage(index, promize_images_id) {
        let promize_attribute_values = this.state.promize_attribute_values;
        let promize_images = promize_attribute_values.promize_images;
        promize_images.splice(index, 1);
        const newObj = Object.assign({}, promize_attribute_values, {
            promize_images: promize_images
        });
        this.setState({ promize_attribute_values: newObj }, () => {
            this.props.dispatch(deleteAttributeValuesImage({ "ids": promize_images_id }))
        });
    }
    /**
   * Handle Filed Blur
   * @param {HtmlEvent | Object} e Html Elemet
   * @param {string} fieldName Field Name
   */
    handleInputFieldBlur(e) {
        e.preventDefault();
        let data = this.state.promize_attribute_values;
        this.props.dispatch(saveAttributeValues(data))
    }
    optionContent() {
        return <React.Fragment>
            <div className="promize-modal-cover" style={{ "display": "none" }}>
                <div className="promize-modal modal-lg">
                    <div className="promize-modal-header">
                        <h3 className="modal-title">
                            <span>
                                Add New Option
                        </span>
                        </h3>
                    </div>
                    <div className="promize-modal-body">
                        <div className="promize-form-group mt-10">
                            <div className="df-wrap">
                                <div className="wd-50">
                                    <div className="mr-15">
                                        <Input {...this.props}
                                            fieldName={'option_name'}
                                            value={this.state.promize_attribute_values.option_name}
                                            labelName={"Option Name"}
                                            onChange={(e) => { this.handleFieldChange(e, "option_name") }}
                                            validation={false}
                                            onBlur={e => this.handleInputFieldBlur(e)}
                                        />
                                    </div>
                                </div>
                                <div className="wd-50">
                                    <Input {...this.props}
                                        fieldName={'option_code'}
                                        value={this.state.promize_attribute_values.option_code}
                                        labelName={"Option Code"}
                                        onChange={(e) => { this.handleFieldChange(e, "option_code") }}
                                        validation={false}
                                        onBlur={e => this.handleInputFieldBlur(e)}
                                    />
                                </div>
                                <div className="wd-50">
                                    <div className="mr-15">
                                        <Input {...this.props}
                                            fieldName={'option_value'}
                                            value={this.state.promize_attribute_values.option_value}
                                            labelName={"Option Value"}
                                            onChange={(e) => { this.handleFieldChange(e, "option_value") }}
                                            validation={false}
                                            onBlur={e => this.handleInputFieldBlur(e)}
                                        />
                                    </div>
                                </div>
                                <div className="wd-50">
                                    <Input {...this.props}
                                        fieldName={'option_sku'}
                                        value={this.state.promize_attribute_values.option_sku}
                                        labelName={"SKU"}
                                        onChange={(e) => { this.handleFieldChange(e, "option_sku") }}
                                        validation={false}
                                        onBlur={e => this.handleInputFieldBlur(e)}
                                    />
                                </div>
                                <div className="wd-50">
                                    <div className="mr-15">
                                        <Input {...this.props}
                                            fieldName={'price'}
                                            value={this.state.promize_attribute_values.option_price ? this.state.promize_attribute_values.option_price : ''}
                                            labelName={'Price'}
                                            onChange={(e) => { this.handleFieldChange(e, "option_price") }}
                                            validation={false}
                                            onBlur={e => this.handleInputFieldBlur(e)}
                                            inputType={'number'}
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="df">
                                <div className="wd-50">
                                    <div className="promize-group-upload mr-15">
                                        <ImageUploadMultilple {...this.props} {...this.state}
                                            setImageKey={this.setImageKey}
                                            callBack={true}
                                            labelName={" Option image"}
                                            fieldName={'image_file'}
                                            dataName={'image_file'}
                                            validation={false}
                                            imageType={"option_image"}
                                            value={this.state.promize_attribute_values.promize_images}
                                            deleteImage={this.deleteImage}
                                        />
                                    </div>
                                </div>
                                <div className="wd-50">
                                    <ImageUpload {...this.props}
                                        setImageKey={this.setImageKey}
                                        callBack={true}
                                        labelName={"Option thumbnail image"}
                                        fieldName={'image_file'}
                                        dataName={'image_file'}
                                        validation={false}
                                        imageType={"option_thumbnail_image"}
                                        value={this.state.promize_attribute_values.option_thumbnail_image}
                                        image_url={this.state.promize_attribute_values.option_thumbnail_image ? this.state.promize_attribute_values.option_thumbnail_image.image_url : ''}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="promize-modal-footer df df-end">
                        <button className="btn btn-promize-secondary" onClick={(e) => { this.hideOptionContentPopup(e) }}>Cancel</button>
                        <button className="btn btn-promize-primary" onClick={(e) => { this.handleAddOption(e) }}>Add</button>
                    </div>
                </div>
            </div>
        </React.Fragment>
    }
    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside);
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }

    setWrapperRef(node) {
        this.wrapperRef = node;
    }

    handleClickOutside(event) {
        if (this.wrapperRef && !this.wrapperRef.contains(event.target)) {
            $('.portlet-dropdown-menu').removeClass('show');
            $('.portlet-dropdown-menu').addClass('hide');
        }
    }
    saveForm(e) {
        e.preventDefault();
        if (this.props.state.fields.attribute_type == "clipart") {
            let clipart_values = this.props.state.fields.promize_attribute_clipart_values;
            clipart_values['promize_attribute_id'] = this.props.state.fields.promize_attribute_id;
            if (!clipart_values.clipart_name || !clipart_values.promize_domain_clipart_category_id) {
                toastr.error("Add all the required fields marked as *")
            } else {
                this.props.dispatch(saveAttributeClipartValues(clipart_values));
                this.props.dispatch(save(this.props.state.fields));
                toastr.success("Records saved successfully");
                this.props.history.push('/attributes');
            }
        } else if (this.props.state.fields.attribute_type == "image") {
            let image_values = this.props.state.fields.promize_attribute_image_values
            image_values['promize_attribute_id'] = this.props.state.fields.promize_attribute_id;
            if (!image_values.image_name || !image_values.image_format) {
                toastr.error("Add all the required fields marked as *")
            } else {
                this.props.dispatch(saveAttributeUploadImageValues(image_values));
                this.props.dispatch(save(this.props.state.fields));
                toastr.success("Records saved successfully");
                this.props.history.push('/attributes');
            }
        } else if (this.props.state.fields.attribute_type == "text") {
            let text_values = this.props.state.fields.promize_attribute_text_values;
            text_values['promize_attribute_id'] = this.props.state.fields.promize_attribute_id;
            if (!text_values.text_name || !text_values.font_ids || !text_values.default_font_family || !text_values.font_size_ids || !text_values.default_font_size || !text_values.font_color_ids || !text_values.default_color) {
                toastr.error("Add all the required fields marked as *")
            } else {
                this.props.dispatch(saveTextValues(text_values));
                this.props.dispatch(save(this.props.state.fields));
                toastr.success("Records saved successfully");
                this.props.history.push('/attributes');
            }
        } else if (this.props.state.fields.attribute_type == "option") {
            this.props.dispatch(save(this.props.state.fields));
            toastr.success("Records saved successfully");
            this.props.history.push('/attributes');
        }
    }
    deleteAttributeValues(e, index, promize_attribute_values_id) {
        e && e.preventDefault();
        let popup = {};
        popup.title = "Remove Option";
        popup.content = 'Do you want to remove this option?';
        popup.submit = () => {
            let fields = this.props.state.fields;
            let promize_attribute_values = fields.promize_attribute_values;
            let selected_index = -1;
            promize_attribute_values.map((value, attr_index) => {
                if (value.promize_attribute_values_id == promize_attribute_values_id) {
                    selected_index = attr_index
                }
            });
            (selected_index !== -1) && promize_attribute_values.splice(selected_index, 1);
            const newObj = Object.assign({}, fields, {
                promize_attribute_values: promize_attribute_values
            });
            this.props.currentComponent.setState({ fields: newObj }, () => {
                this.props.dispatch(deleteAttributeValues({ "ids": [promize_attribute_values_id] }))
            });
        }
        this.setState({ popup })

    }
    editAttributeValues(e, promize_attribute_values_id) {
        e.preventDefault();
        let promize_attribute_values = this.props.state.fields.promize_attribute_values;
        let attribute_values = {};
        promize_attribute_values.map((value, index) => {
            if (value.promize_attribute_values_id == promize_attribute_values_id) {
                attribute_values = value;
            }
        });
        this.setState({ promize_attribute_values: attribute_values, enableEditAttribute: true }, () => {
            $('.promize-modal-cover').show();
        })
    }

    handleFontSize(value) {
        let temp_arr = [];
        let font_size_ids = value;
        let selected_font_size = [];
        const { promize_attribute_text_values } = this.state;
        font_size_ids.map((fontsize, index) => {
            temp_arr.push(fontsize.value);
            selected_font_size.push({ 'font_size_id': fontsize.value, 'font_size': fontsize.label })
        })
        let ids = temp_arr.length < 2 ? temp_arr.toString() : temp_arr.join(',')
        promize_attribute_text_values['font_size_ids'] = ids;
        promize_attribute_text_values['selected_font_size'] = selected_font_size;
        this.setState({ promize_attribute_text_values });

    }
    handleFontColor(value) {
        let temp_arr = [];
        let font_color_ids = value;
        let selected_font_color = [];
        const { promize_attribute_text_values } = this.state;
        font_color_ids.map((fontcolor, index) => {
            temp_arr.push(fontcolor.value);
            selected_font_color.push({ 'promize_font_color_id': fontcolor.value, 'font_color_name': fontcolor.label })
        })
        let ids = temp_arr.length < 2 ? temp_arr.toString() : temp_arr.join(',')
        promize_attribute_text_values['font_color_ids'] = ids;
        promize_attribute_text_values['selected_font_color'] = selected_font_color;
        this.setState({ promize_attribute_text_values });
    }
    closePopup = () => {
        this.setState({ popup: false })
    }
    render() {
        return <React.Fragment>
            {this.state.popup && <Popup {...this.state.popup} submitButton="Remove" cancel={this.closePopup} />}
            <form onSubmit={this.props.form.handleSubmit} method="POST">
                <div className="promize-card mb-20">
                    <div className="promize-card-header">
                        <div className="promize-card-title mb-10">
                            <h3>
                                <span>{this.props.title}</span>
                            </h3>
                        </div>
                    </div>
                    <div className="promize-card-body">
                        <div className="df">
                            <div className="wd-30 mr-15">
                                <Input {...this.props}
                                    fieldName={'attribute_name'}
                                    value={this.props.state.fields.attribute_name}
                                    labelName={"Attribute Name"}
                                    validation={true}
                                />
                            </div>
                            <div className="wd-30 mr-15">
                                <Input {...this.props}
                                    fieldName={'attribute_code'}
                                    value={this.props.state.fields.attribute_code}
                                    labelName={"Attribute Code"}
                                    validation={false}
                                />
                            </div>
                            <div className="wd-30">
                                <Switchtoggle
                                    {...this.props}
                                    labelName={'Status'}
                                    fieldName={'status'}
                                    dataName={'status'}
                                    id={1}
                                    value={this.props.statusValue}
                                    spanLabel={{ first: "Active", second: "Inactive" }}
                                    checkValue={{ first: true, second: false }}
                                    validation={false}
                                    className={"toggle-aa"}
                                />
                            </div>
                        </div>
                        <div className="wd-100">
                            <TypeSelection {...this.props}
                                labelName={'Attribute Type'}
                                fieldName={'attribute_type'}
                                dataName={'attribute_type'}
                                value={this.props.state.fields.attribute_type}
                                checkValue={[
                                    { label: "Option", value: "option" },
                                    { label: "Image", value: "image" },
                                    { label: "Text", value: "text" },
                                    { label: "Clipart", value: "clipart" }
                                ]}
                                validation={false}
                                disabled={this.props.state.fields.promize_attribute_id ? true : false}
                            />
                        </div>
                        <div className="df df-end">
                            {!this.props.state.fields.promize_attribute_id && <SubmitButton className="btn btn-promize-primary" text={"Next"} response={this.props.state.fields} />}
                        </div>
                    </div>

                </div>
                <div className="promize-card">
                    {
                        this.props.state.fields.attribute_type == "clipart" && this.props.state.attribute_saved &&
                        <Clipart {...this.props} />
                    }
                    {
                        this.props.state.fields.attribute_type == "image" && this.props.state.attribute_saved &&
                        <Image {...this.props} />
                    }

                    {
                        this.props.state.fields.attribute_type == "option" && this.props.state.attribute_saved &&
                        <React.Fragment>

                            <div className="promize-card-header">
                                <div className="promize-card-title">
                                    <h3>
                                        <span>Options</span>
                                    </h3>
                                </div>
                                <div className="promize-card-action df-end">
                                    <div className="tab-links">
                                        <button className="btn btn-links" onClick={(e) => { this.showOptionContentPopup(e) }}><i className="fa fa-plus"></i> Add Option</button>
                                    </div>
                                </div>
                            </div>
                            <div className="promize-card-body">
                                <table className="promize-table table-hover table-action">
                                    <thead>
                                        <tr>
                                            <th>Name</th>
                                            <th>Code</th>
                                            <th>Image</th>
                                            <th>Thumbnail</th>
                                            <th>SKU</th>
                                            <th>Price ($)</th>
                                            <th>Actions</th>
                                        </tr>
                                    </thead>
                                    <tbody>

                                        {this.props.state.fields.promize_attribute_values.length == 0 &&
                                            <tr><td colSpan="7"><p className="promize-no-records"> No records found</p></td></tr>
                                        }
                                        {this.props.state.fields.promize_attribute_values.length > 0 && this.props.state.fields.promize_attribute_values.map((data, index) => {
                                            return <tr key={index}>
                                                <td>{data.option_name ? data.option_name : '-'}</td>
                                                <td>{data.option_code ? data.option_code : '-'}</td>
                                                <td><div className="promize-tbl-mi-cvr"><ul className="promize-milist">
                                                    {(data.promize_images.length > 0) && data.promize_images.map((img_data, index) => {
                                                        if (img_data.image_type == 'option_thubnail_image') {
                                                            return '';
                                                        }
                                                        return <li key={index}>
                                                            {img_data.image_url && <img src={app.APP_IMAGE_BASE_URL + img_data.image_url} className="promize-tbl-img" alt={" "} />}
                                                        </li>
                                                    })}
                                                    {!(data.promize_images.length > 0) && <li>{"-"}</li>}
                                                </ul></div></td>
                                                <td><div className="promize-tbl-mi-cvr">
                                                    {data.option_thumbnail_image && data.option_thumbnail_image.image_url &&
                                                        <img src={app.APP_IMAGE_BASE_URL + data.option_thumbnail_image.image_url} className="promize-tbl-img-sm" alt={" "} />}
                                                    {!(data.option_thumbnail_image) && <div>{"-"}</div>}
                                                </div>
                                                </td>
                                                <td>{data.option_sku ? data.option_sku : '-'}</td>
                                                <td>{data.option_price ? data.option_price : '-'}</td>
                                                <td>
                                                    <div className="table-actions">
                                                        <button className="btn-tbl edit" onClick={(e) => { this.editAttributeValues(e, data.promize_attribute_values_id) }}><i className="fa fa-pencil-alt"></i></button>
                                                        <button className="btn-tbl delete" onClick={(e) => { this.deleteAttributeValues(e, index, data.promize_attribute_values_id) }}><i className="fa fa-trash-alt"></i></button>
                                                    </div>
                                                </td>
                                            </tr>
                                        })}
                                    </tbody>
                                </table>
                            </div>
                        </React.Fragment>
                    }
                    {
                        this.props.state.fields.attribute_type == "text" && this.props.state.attribute_saved &&
                        <Text {...this.props} />
                    }
                    {this.props.state.attribute_saved &&
                        <div className="promize-footer-actions">
                            <NavLink to="/attributes" className="btn btn-promize-secondary">{"Cancel"}</NavLink>
                            <button className="btn btn-promize-primary" onClick={(e) => { this.saveForm(e) }}>{"Save"}</button>
                        </div>
                    }
                </div> {this.optionContent()}
            </form >
        </React.Fragment>
    }
}
function mapStateToProps(state) {
    return {
        imageUpload: state.imageUpload,
        attributeValues: state.attributeValues,
        attributeValuesImages: state.attributeValuesImages,
    };
}


export default connect(mapStateToProps)(Forms);