import React from 'react';

class CheckBox extends React.Component {
  constructor (props) {
    super(props);
    this.onChangeHandler = this.onChangeHandler.bind(this);
    //this.currentComponent = this.props.currentComponent;
  }
  onChangeHandler(e){
    if(this.props.onChange){
      this.props.onChange(e);
    }else{
      this.props.form.handleFieldsChange(e)
    }
  }

  render() {
    const checkList = (<React.Fragment> 
      <div className={"form-group check-mainbox "}>
      <label className="top mb-10">{this.props.labelName}</label>
        {this.props.dataList.map((data, index)=>{
          return <label className="promize-checkbox" key={index} htmlFor={data.value+this.props.dataName}>
            <input type="checkbox"
              id={data.value+this.props.dataName}
              onChange={(e)=>this.onChangeHandler(e)}
              checked={(this.props.value.includes(data.value)) ? true : false} 
              value={data.value} 
              name={this.props.fieldName} 
            />
            <span> {data.label}</span>  
          </label>
        })}
        </div>
        </React.Fragment>);

    return (checkList);
  }
}

export default CheckBox;