import React, { Component } from 'react';
import { connect } from 'react-redux';
import _ from 'lodash';
import { getAllFonts } from '../../../actions/fontfamily';
import { getAllFontSizes } from '../../../actions/fontsize';
import { getAllFontColors } from '../../../actions/fontcolor';
import Input from '../../../components/form/input';
import MultiSelect from '../../../components/form/MultiSelect';
import { SimpleSelect } from "../../../components/form/SimpleSelect";
import RadioButton from '../../../components/form/radioButton';
import CheckBox from '../../../components/form/CheckBox';

class Text extends Component {
    constructor(props) {
        super(props)
        this.state = {
            toggle: false,
            "selected_font_family": [],
            "selected_font_size": [],
            "selected_font_color": [],
            "fontsList": [],
            "fontSizes": [],
            "fontColors": [],
            "additional_data" : {
                "long_character" : 1,
                "text_color" : JSON.stringify([1])
            }
        }
        this.handleFontsSelect = this.handleFontsSelect.bind(this)
        this.onChangeHandler = this.onChangeHandler.bind(this)
        this.handleFontColor = this.handleFontColor.bind(this);
        this.handleFontSize = this.handleFontSize.bind(this);
        
    }
    componentWillMount() {
        this.props.dispatch(getAllFonts());
        this.props.dispatch(getAllFontSizes());
        this.props.dispatch(getAllFontColors());
    }
    componentWillReceiveProps(props) {
        let { fontsList, fontSizes, fontColors } = this.state
        if (!props.fonts.isFetching && !props.fonts.isError && this.props.fonts.isFetching !== props.fonts.isFetching) {
            let fonts = props.fonts.response.data, lists=[];
            fonts.length > 0 &&  fonts.map((font,index)=>{
                let fontlist = {};
                fontlist.label = font.font_name;
                fontlist.value = font.font_id;
                lists.push(fontlist);
            })
            this.setState({ fontsList: lists
             },()=>{
                let {font_ids} = props.state.fields.promize_attribute_text_values;
                let fontsList = this.state.fontsList
                let selected_font_family = [];
                let fontIdArray = font_ids ? font_ids.split(',') : [];
                fontIdArray.length > 0 && fontsList && fontIdArray.map((fontId,index)=>{
                    let fontFamily = _.find(fontsList, ['value', parseInt(fontId)]); 
                    fontFamily && selected_font_family.push(fontFamily)
                })
                this.setState({selected_font_family})
            })
        }
        else if (!props.fontsizes.isFetching && !props.fontsizes.isError && this.props.fontsizes.isFetching !== props.fontsizes.isFetching) {
            let fontsizes = props.fontsizes.response.data, lists = [];
            fontsizes.length >0 && fontsizes.map((size, index) => {
                let sizes = {};
                sizes.label = size.font_size;
                sizes.value = size.font_size_id;
                lists.push(sizes);
            })
            this.setState({
                fontSizes: lists
            }, () => {
                let { font_size_ids } = props.state.fields.promize_attribute_text_values;
                let selected_font_size = [];
                let fontSizes = this.state.fontSizes
                let fontSizeArray = font_size_ids ? font_size_ids.split(',') : [];
                fontSizeArray.length > 0 && fontSizes && fontSizeArray.map((fontId,index)=>{
                    let fontSize = _.find(fontSizes, ['value', parseInt(fontId)]); 
                    fontSize && selected_font_size.push(fontSize)
                })
                this.setState({selected_font_size})
            })
        }
        else if (!props.fontcolors.isFetching && !props.fontcolors.isError && this.props.fontcolors.isFetching !== props.fontcolors.isFetching) {
            let fontcolors = props.fontcolors.response.data, lists = [];
            fontcolors.length >0 && fontcolors.map((color, index) => {
                let colors = {};
                colors.label = color.font_color_name + '(' + color.font_color_code + ')';
                colors.topLabel = color.font_color_name;
                colors.subLabel = color.font_color_code
                colors.value = color.promize_font_color_id;
                lists.push(colors);
            })
            this.setState({
                fontColors: lists
            }, () => {
                let { font_color_ids } = props.state.fields.promize_attribute_text_values;
                let selected_font_color = [];
                let fontColors = this.state.fontColors
                let fontColorArray = font_color_ids ? font_color_ids.split(',') : [];
                fontColorArray.length > 0 && fontColors && fontColorArray.map((fontId,index)=>{
                    let fontColor = _.find(fontColors, ['value', parseInt(fontId)]); 
                    fontColor && selected_font_color.push(fontColor)
                })
                this.setState({selected_font_color})
            })
        }
    }
    /**
     * Handle the Text Fields Changes
     * @param {HtmlEvent} e 
     * @param {string} fieldName 
     */
    handleTextFields(e, fieldName) {
        e.preventDefault();
        let fields = this.props.state.fields
        let { promize_attribute_text_values } = fields
        promize_attribute_text_values[fieldName] = e.target.value ? e.target.value : '';
        const newObj = Object.assign({}, fields, {
            promize_attribute_text_values: promize_attribute_text_values
        });
        this.props.currentComponent.setState({ fields: newObj })
    }

    /**
     * Handle the font select
     * @param {object} value 
     */
    handleFontsSelect(value) {
        let temp_arr = [];
        let font_ids = value;
        font_ids && font_ids.map((fontid, index) => {
            temp_arr.push(fontid.value);
        })
        let ids = temp_arr.length < 2 ? temp_arr.toString() : temp_arr.join(',')
        this.setState({ selected_font_family: value });

        let fields = this.props.state.fields
        const { promize_attribute_text_values } = fields
        promize_attribute_text_values['font_ids'] = ids;
        const newObj = Object.assign({}, fields, {
            promize_attribute_text_values: promize_attribute_text_values
        });
        this.props.currentComponent.setState({ fields: newObj })
    }

    handleFontSize(value) {
        let temp_arr = [];
        let font_size_ids = value;
        font_size_ids.map((fontsize, index) => {
            temp_arr.push(fontsize.value);
        })
        let ids = temp_arr.length < 2 ? temp_arr.toString() : temp_arr.join(',')
        this.setState({ selected_font_size: value });

        let fields = this.props.state.fields
        const { promize_attribute_text_values } = fields
        promize_attribute_text_values['font_size_ids'] = ids;
        const newObj = Object.assign({}, fields, {
            promize_attribute_text_values: promize_attribute_text_values
        });
        this.props.currentComponent.setState({ fields: newObj })

    }
    handleFontColor(value) {
        let temp_arr = [];
        let font_color_ids = value;
        font_color_ids.map((fontcolor, index) => {
            temp_arr.push(fontcolor.value);
        })
        let ids = temp_arr.length < 2 ? temp_arr.toString() : temp_arr.join(',')
        this.setState({ selected_font_color: value });

        let fields = this.props.state.fields
        const { promize_attribute_text_values } = fields
        promize_attribute_text_values['font_color_ids'] = ids;
        const newObj = Object.assign({}, fields, {
            promize_attribute_text_values: promize_attribute_text_values
        });
        this.props.currentComponent.setState({ fields: newObj })
    }
    /**
     * On Change Handle
     * @param {HtmlEvent} e 
     * @param {string} attributeType 
     */
    onChangeHandler(e, attributeType) {
        let fields = this.props.state.fields
        const { promize_attribute_text_values } = fields
        promize_attribute_text_values[attributeType] = e.target.value ? parseInt(e.target.value) : '';
        const newObj = Object.assign({}, fields, {
            promize_attribute_text_values: promize_attribute_text_values
        });
        this.props.currentComponent.setState({ fields: newObj })
    }

    radioButtonHandler = (e) =>{
        let fields = this.props.state.fields;
        let additional_data = this.state.additional_data;
        const { promize_attribute_text_values } = fields
        let fieldName = e.target ? e.target.getAttribute('name') : '';
        if(fieldName){
            additional_data[fieldName] = e.target.value ? e.target.value : 1
            let obj = JSON.stringify(additional_data);
            promize_attribute_text_values['additional_data'] = obj;
            const newObj = Object.assign({}, fields, {
                promize_attribute_text_values: promize_attribute_text_values
            });
            this.props.currentComponent.setState({ fields: newObj })
        }
    }
    checkBoxHandler = e =>{
        let fields = this.props.state.fields;
        let additional_data = this.state.additional_data;
        const { promize_attribute_text_values } = fields
        let fieldName = e.target ? e.target.getAttribute('name') : '';
        let tempObj = JSON.parse(additional_data[fieldName]);
        if(fieldName && !tempObj.includes(parseInt(e.target.value))){    
            tempObj.push(parseInt(e.target.value))
            additional_data[fieldName] = JSON.stringify(tempObj);   
        }else if(fieldName && tempObj.includes(parseInt(e.target.value))){
            tempObj = tempObj.filter((val)=>{
                return parseInt(val) != parseInt(e.target.value)
            })
            additional_data[fieldName] = JSON.stringify(tempObj);  
        }
        let obj = JSON.stringify(additional_data);
        promize_attribute_text_values['additional_data'] = obj;
        const newObj = Object.assign({}, fields, {
            promize_attribute_text_values: promize_attribute_text_values
        });
        this.props.currentComponent.setState({ fields: newObj })
    }

    openSettings = (e) =>{
        e.preventDefault()
        this.setState({ toggle: !this.state.toggle })
    }
    render() {
        return <React.Fragment>
            <div className="promize-card-header">
                <div className="promize-card-title">
                    <h3>
                        <span>Text</span>
                    </h3>
                </div>
            </div>
            <div className="promize-card-body">
            <div className="df-wrap">
                <div className="wd-33">
                    <div className="mr-15">
                        <Input {...this.state} {...this.props}
                            fieldName={'text_name'}
                            value={this.props.state.fields.promize_attribute_text_values.text_name}
                            labelName={"Text Name"}
                            validation={true}
                            onChange={(e) => { this.handleTextFields(e, "text_name") }}
                        />
                    </div>
                </div>
                <div className="wd-33">
                    <div className="mr-15">
                        <Input {...this.state}{...this.props}
                            fieldName={'text_sku'}
                            value={this.props.state.fields.promize_attribute_text_values.text_sku}
                            labelName={"SKU"}
                            validation={false}
                            onChange={(e) => { this.handleTextFields(e, "text_sku") }}
                        />
                    </div>
                </div>
                <div className="wd-33">
                    <div className="mr-15">
                        <Input {...this.state}{...this.props}
                            fieldName={'text_price'}
                            value={this.props.state.fields.promize_attribute_text_values.text_price}
                            labelName={"Price"}
                            validation={false}
                            onChange={(e) => { this.handleTextFields(e, "text_price") }}
                            inputType={'number'}
                        />
                    </div>
                </div>
                <div className="wd-66">
                    <div className="mr-15">
                        <MultiSelect
                            labelName={"Font Family"}
                            value={this.state.selected_font_family}
                            selected={this.state.font_ids}
                            onChange={this.handleFontsSelect}
                            options={this.state.fontsList}
                            isMulti={true}
                            validation={true}
                        />
                    </div>
                </div>
                <div className="wd-33">
                    <div className="mr-15">
                        <SimpleSelect
                            onChangeHandler={(e) => this.onChangeHandler(e, 'default_font_family')}
                            data={this.state.selected_font_family || []}
                            optionText="label"
                            optionValue='value'
                            emptyOptionText="Select Default Font Family..."
                            disabled={!(this.state.selected_font_family && this.state.selected_font_family.length > 0)}
                            value={this.props.state.fields.promize_attribute_text_values.default_font_family}
                            name="Default Font"
                            labelName={"Default Font Family"}
                            validation={true}
                        />
                    </div>
                </div>
                <div className="wd-66">
                    <div className="mr-15">
                        <MultiSelect
                            value={this.state.selected_font_size}
                            onChange={this.handleFontSize}
                            options={this.state.fontSizes}
                            isMulti={true}
                            labelName={"Font Size"}
                            validation={true}
                        />
                    </div>
                </div>
                <div className="wd-33">
                    <div className="mr-15">
                        <SimpleSelect
                            onChangeHandler={(e) => this.onChangeHandler(e, 'default_font_size')}
                            data={this.state.selected_font_size || []}
                            optionText="label"
                            optionValue='value'
                            emptyOptionText="Select Default Font Size..."
                            value={this.props.state.fields.promize_attribute_text_values.default_font_size}
                            name="Default Size"
                            disabled={!(this.state.selected_font_size && this.state.selected_font_size.length > 0)}
                            labelName={"Default Font Size"}
                            validation={true}
                        />
                    </div>
                </div>
                <div className="wd-66">
                    <div className="mr-15">
                        <MultiSelect
                            value={this.state.selected_font_color}
                            onChange={this.handleFontColor}
                            options={this.state.fontColors}
                            isMulti={true}
                            subLabel={true}
                            labelName={"Font Color"}
                            validation={true}
                        />
                    </div>
                </div>
                <div className="wd-33">
                    <div className="mr-15">
                        <SimpleSelect
                            onChangeHandler={(e) => this.onChangeHandler(e, 'default_color')}
                            data={this.state.selected_font_color || []}
                            optionText="label"
                            optionValue='value'
                            emptyOptionText="Select Default Font Color..."
                            value={this.props.state.fields.promize_attribute_text_values.default_color}
                            name="Default Color"
                            labelName={"Default Font Color"}
                            disabled={!(this.state.selected_font_color && this.state.selected_font_color.length > 0)}
                            validation={true}
                        />
                    </div>
                </div>
            </div>
            </div>
            {/* <div className="promize-widget-header">
                <h3 className="promize-widget-title">
                        {"Additional Settings"}
                </h3>
                <div className="promize-widget-toggle">
                    <button className={"btn " + (this.state.toggle ? "" : "toggle")} onClick={(e) => {this.openSettings(e) }} ><i className="fa fa-angle-down"></i></button>
                </div>
            </div> */}
            {this.state.toggle && <div className="promize-widget-body">
                <RadioButton {...this.props}
                    labelName={'How would you like to handle the longer character validations?'}
                    fieldName={'long_character'}
                    dataName={'long_character'}
                    value={this.state.additional_data.long_character}
                    checkValue={[
                        { label: "Automatically hide the additional characters out of the printing area", value: 1 },
                        { label: "Stop the customer from typing additional characters out of printing area", value: 2 },
                        { label: "Throw error message when the character goes out of printing area", value: 3 },
                        { label: "Automatically reduce the font size to fit inside the printing area", value: 4 },
                        { label: "Automatically go to next line if it reaches the edge of the printing area", value: 5 },
                        { label:'Limit the text with X number of characters eg (10 or 15)', value:6}
                    ]}
                    validation={false}
                    onChange={this.radioButtonHandler}
                    promize_tab_id = {''}
                />
                 <CheckBox {...this.props}
                    dataList={[
                        { label: "Specific colors", value: 1 },
                        { label: "Color Picker", value: 2 },
                    ]}
                    fieldName={'text_color'}
                    dataName={'text_color'}
                    value={JSON.parse(this.state.additional_data.text_color)}
                    onChange={this.checkBoxHandler}
                    labelName={"Text Color"}
                />

            </div>}
        </React.Fragment>
    }
}
function mapStateToProps(state) {
    return {
        attributeValues: state.attributeValues,
        fonts: state.fonts,
        fontsizes: state.fontsizes,
        fontcolors: state.fontcolors
    };
}
export default connect(mapStateToProps)(Text);