import React from 'react';

class switchtoggle extends React.Component {
  constructor(props) {
    super(props)
    this.changeButton = this.changeButton.bind(this);
  }

  changeButton(e) {
    let value;
    if (e.target.checked) {
      value = this.props.checkValue['first'];
    } else {
      value = this.props.checkValue['second'];
    }
    this.props.form.handleFieldsChangeByPlugin && this.props.form.handleFieldsChangeByPlugin(e.target.name, value)
  }

  render() {
    const changeMethod = this.props.onChange ? this.props.onChange : this.changeButton;
    return (<div className={this.props.classWrapper ? this.props.classWrapper : "form-group" + " check-mainbox"}>
      <div className={"promize-toggle " + this.props.className || this.props.className}>
        <label>{this.props.labelName}</label>
        <input className="tgl tgl-light" id={this.props.id} onChange={changeMethod} checked={this.props.value == this.props.checkValue['first'] ? true : false} name={this.props.fieldName} type="checkbox" />
        <label className="tgl-btn" htmlFor={this.props.id}></label>
      </div>
    </div>);
  }
}

export default switchtoggle;
