import { CLIPART, CLIPARTS, CLIPART_VALUES, CLIPART_DELETE, CLIPART_VALUES_DELETE, IMAGE_UPLOAD, PENDING, FULFILLED, REJECTED } from "../../utility/constants";
import { app } from "../../config/app";

export function save(data) {
    let method = 'POST', url='createRecord';

    data = {
        ...data,
        status : true ? 1 : 0,
        modelName: "promize_domain_clipart_category",
        idKey: "promize_domain_clipart_category_id"
    }
    if (data.promize_domain_clipart_category_id) {
        method = 'PUT';
        url = 'updateRecord';
        delete data.created_date;
        delete data.updated_date;
    }
    return {
        types: [`${CLIPART}_${PENDING}`, `${CLIPART}_${FULFILLED}`, `${CLIPART}_${REJECTED}`],
        method: method,
        url: url,
        data: data,
        payload: { receiveType: 'postData' }
    }
}
export function saveClipartValues(data) {
    let method = 'POST', url = 'createRecord';
    data = {
        ...data,
        modelName: "promize_clipart_category_images",
        idKey: "promize_clipart_category_images_id"
    }
    if (data.promize_clipart_category_images_id) {
        method = 'PUT';
        url = 'updateRecord';
        delete data.created_date;
        delete data.updated_date;
    }
    return {
        types: [`${CLIPART_VALUES}_${PENDING}`, `${CLIPART_VALUES}_${FULFILLED}`, `${CLIPART_VALUES}_${REJECTED}`],
        method: method,
        url: url,
        data: data,
        payload: { receiveType: 'postData' }
    }
}
export function get(id) {
    const method = 'GET';
    return {
        types: [`${CLIPART}_${PENDING}`, `${CLIPART}_${FULFILLED}`, `${CLIPART}_${REJECTED}`],
        method: method,
        url: 'getClipartWithPK/' + id,
        data: {},
        payload: { receiveType: 'editData' }
    }
}

export function getAllCliparts() {
    let data = {
        modelName:"promize_domain_clipart_category",
        idKey:"promize_domain_clipart_category_id",
        domain_id : app.DOMAIN_ID
    }
    const request = {
        types: [`${CLIPARTS}_${PENDING}`, `${CLIPARTS}_${FULFILLED}`, `${CLIPARTS}_${REJECTED}`],
        method: 'POST',
        url: 'getAllRecords',
        data: data,
    }

    return request;
}

export function getClipartLists() {
    const request = {
        types: [`${CLIPARTS}_${PENDING}`, `${CLIPARTS}_${FULFILLED}`, `${CLIPARTS}_${REJECTED}`],
        method: 'GET',
        url: 'getAttributeLists/' + app.DOMAIN_ID,
    }

    return request;
}

export function uploadClipartImage(file, typeName, name) {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('name', typeName);
    return {
        types: [`${IMAGE_UPLOAD}_${PENDING}`, `${IMAGE_UPLOAD}_${FULFILLED}`, `${IMAGE_UPLOAD}_${REJECTED}`],
        method: 'POST',
        requestBodyType: 'FormData',
        url: 'uploadClipartImage',
        data: formData,
        payload: { receiveType: 'postData' }
    }
}
export function deleteClipartValues(data){
    data = {
        ...data,
        modelName:"promize_clipart_category_images",
        idKey:"promize_clipart_category_images_id"
    }
	if(data.promize_clipart_category_images_id){
        delete data.created_date;
        delete data.updated_date;
	}
    return {
        types: [`${CLIPART_VALUES_DELETE}_${PENDING}`, `${CLIPART_VALUES_DELETE}_${FULFILLED}`, `${CLIPART_VALUES_DELETE}_${REJECTED}`],
        method: 'DELETE',
        url: 'deleteRecord',
        data: data,
        payload: {receiveType:'postData'}
    }
}


export function changeStatus(actionDetails){
	let {url, actionType, params} = actionDetails;
    params.status = (params.status == 1) ? 0 : 1;
    delete params.created_date;
    delete params.updated_date;
    params = {
        ...params,
        modelName:"promize_domain_clipart_category",
        idKey:"promize_domain_clipart_category_id",
    }
	return {
		types: [`${actionType}_${PENDING}`, `${actionType}_${FULFILLED}`, `${actionType}_${REJECTED}`],
		method: 'PUT',
		url: `${url}`,
		payload: {},
		data: params
	}
}
