import React, { Component } from 'react';

class TypeSelection extends Component {
  constructor(props) {
    super(props);
    this.onChangeHandler = this.onChangeHandler.bind(this);
  }
  onChangeHandler = (e) => {
    if (this.props.onChange) {
      this.props.onChange(e);
    } else {
      this.props.form.handleFieldsChange(e)
    }
  }

  getRadioData = (label, val, index) => {
    return <label key={index}
      className={"promize-radio promize-attr-radio mr-15" + ((this.props.disabled && (String(this.props.value) !== String(val))) ? " disabled" : "") + (String(this.props.value) === String(val) ? " checked" : "")} 
      htmlFor={val}>
       <input
         disabled={(String(this.props.value) !== String(val)) ? this.props.disabled : ''}
          type="radio"
          id={val}
          data-name={this.props.dataName}
          data-validation-name={this.props.labelName}
          onChange={e => {this.onChangeHandler(e)}}
          checked={(String(this.props.value) === String(val)) ? 'checked' : ''} 
          value={val}
          className="check"
          name={this.props.fieldName} 
        />
        <span className="chk-cover"></span>
        <span className="chk-span"></span>
        <span className="chk-icn">
          {val == 'option' && <i className="fa fa-cube"></i>}
          {val == 'image' && <i className="fa fa-images"></i>}
          {val == 'text' && <i className="fa fa-font"></i>}
          {val == 'clipart' && <i className="fa fa-grin-hearts"></i>}
        </span> 
         {label}
     </label>
  }

  render() {
    let checkValues = this.props.checkValue;
    let listData = []
    checkValues.map((data, index)=>{
        listData.push(this.getRadioData(data.label, data.value, index))
    })

    return (<div className={"form-group check-mainbox "}>
      <label className="top mb-10">{this.props.labelName}{(this.props.validation !== false) && <sup>*</sup>}</label>
      <div className={"inputgroup " + (this.props.separate || "")}>
        <div>
        {listData.map((data, index)=>{
          return data
        })
        }
        </div>
        {this.props.form.validation(this.props.fieldName) && <p className="error-msg">{this.props.state.inputErrors[this.props.fieldName].message}</p>}
      </div>
    </div>);

  }
}

export default TypeSelection;