import React, { Component } from 'react';
import { connect } from 'react-redux';
import {app} from '../../config/app';
import noImages from '../../assets/images/no-image.png';
import Dropzone from 'react-dropzone'

class ImageUploadMultilple extends Component {

    constructor(props) {
        super(props);
        this.state = {
            highlight: false
        }
        this.setImageKey = this.setImageKey.bind(this);
        this.change = this.change.bind(this);
        this.onDrop = this.onDrop.bind(this);
        this.onDragOver = this.onDragOver.bind(this);
        this.onDragLeave = this.onDragLeave.bind(this);
    }
    

    setImageKey(){
        if(this.props.callBack == true){
            this.props.setImageKey(this.props.imageType)
        }
    }

    change(e){
        this.props.form.handleFileSelect(e);
        e.target.value = null;  
        this.setImageKey()      
    }
    
    onDrop(acceptedFiles){
        this.props.form.handleFileDrop({"files":acceptedFiles},"image_file")
        this.setImageKey()
        this.setState({highlight:false})
    }

    onDragOver(e){
        this.setState({highlight:true})
    }

    onDragLeave(e){
        this.setState({highlight:false})
    }

    deleteImage(e, index, promize_images_id){
        e.preventDefault();
        this.props.deleteImage(index, promize_images_id)
    }
    render() {
        let highlight = false
        return (<Dropzone onDrop={this.onDrop} onDragOver={this.onDragOver} onDragLeave={this.onDragLeave}>
            {({getRootProps, getInputProps}) => (
                <div className="promize-upload" {...getRootProps()}>
                    <div className="promize-upload-header">
                        <label className="promize-upload-title">{ this.props.labelName || "View Image"}</label>
                        <div className="custom-upload-product">
                            <div className="promize-upload-button">
                                <input {...getInputProps()} type="file" name="image_file" id="image_file" onChange={this.change}/>
                                <span className="file-custom">Upload Image</span>
                            </div>
                        </div>
                    </div>
                    <div className={"promize-upload-content " + (highlight?'promize-highlight':'')}>
                        <div className="promize-content-before-upload">
                            <ul className="promize-group-upload-items">
                                {this.props.value && this.props.value.map((data, index)=>{  
                                    if(data.image_type == 'option_thubnail_image'){
                                        return '';
                                    }
                                    return <li key={index}>
                                        <div className="promize-uploded-image">
                                        <img src={app.APP_IMAGE_BASE_URL + data.image_url} />
                                            <div onClick={(e) => {e.stopPropagation();this.props.deleteImage(index, data.promize_images_id)}} className="promize-upload-delete">
                                                <i className="fa fa-trash-alt"></i>
                                            </div>
                                        </div>
                                    </li>
                                    })
                                }
                            </ul>
                            {this.props.value.length <= 0 && <img src={noImages} />}
                        </div>
                    </div>
                </div>
                )}
            </Dropzone>
            );
    }
}

function mapStateToProps(state) {
    const { image } = state;
    return {
        image
    };
}

export default connect(mapStateToProps)(ImageUploadMultilple);
