import React, { Component } from 'react';
import { connect } from 'react-redux';
import _ from 'lodash';
import Input from '../../../components/form/input';

class Image extends Component {
    constructor(props) {
        super(props)
        this.handleImageFieldChange = this.handleImageFieldChange.bind(this);
    }

    handleImageFieldChange(e, fieldName) {
        e.preventDefault();
        let fields = this.props.state.fields;
        let promize_attribute_image_values = fields.promize_attribute_image_values;
        promize_attribute_image_values[fieldName] = e.target.value;
        const newObj = Object.assign({}, fields, {
            promize_attribute_image_values: promize_attribute_image_values
        });
        this.props.currentComponent.setState({ fields: newObj });
    }

    render() {
        return <React.Fragment>
            <div className="promize-card-header">
                <div className="promize-card-title">
                    <h3>
                        <span>Upload Image</span>
                    </h3>
                </div>
            </div>
            <div className="promize-card-body">
                <div className="df">
                    <div className="wd-30 mr-15">
                        <Input {...this.props}
                            fieldName={'image_name'}
                            value={this.props.state.fields.promize_attribute_image_values.image_name}
                            labelName={"Image Label"}
                            validation={true}
                            onChange={(e) => { this.handleImageFieldChange(e, "image_name") }}
                        />
                    </div>
                    <div className="wd-30 mr-15">
                        <Input {...this.props}
                            fieldName={'image_code'}
                            value={this.props.state.fields.promize_attribute_image_values.image_code}
                            labelName={"Image code"}
                            validation={false}
                            onChange={(e) => { this.handleImageFieldChange(e, "image_code") }}
                        />
                    </div>
                    <div className="wd-30 mr-15">
                        <Input {...this.props}
                            fieldName={'image_price'}
                            value={this.props.state.fields.promize_attribute_image_values.image_price}
                            labelName={"Image Price"}
                            validation={false}
                            inputType={'number'}
                            onChange={(e) => { this.handleImageFieldChange(e, "image_price") }}
                        />
                    </div>
                </div>
                <div className="df">
                <div className="wd-30 mr-15">
                        <Input {...this.props}
                            fieldName={'image_sku'}
                            value={this.props.state.fields.promize_attribute_image_values.image_sku}
                            labelName={"Image SKU"}
                            validation={false}
                            onChange={(e) => { this.handleImageFieldChange(e, "image_sku") }}
                        />
                    </div>
                <div className="wd-30 mr-15">
                        <Input {...this.props}
                            fieldName={'min_upload_size'}
                            value={this.props.state.fields.promize_attribute_image_values.min_upload_size}
                            labelName={"Min Upload Size(in Mb)"}
                            validation={false}
                            inputType={'number'}
                            onChange={(e) => { this.handleImageFieldChange(e, "min_upload_size") }}
                        />
                    </div>
                    <div className="wd-30 mr-15">
                        <Input {...this.props}
                            fieldName={'max_upload_size'}
                            value={this.props.state.fields.promize_attribute_image_values.max_upload_size}
                            labelName={"Max Upload Size(in Mb)"}
                            validation={false}
                            inputType={'number'}
                            onChange={(e) => { this.handleImageFieldChange(e, "max_upload_size") }}
                        />
                    </div>
                    
                </div>
                <div className="df">
                <div className="wd-30 mr-15">
                        <Input {...this.props}
                            fieldName={'image_format'}
                            value={this.props.state.fields.promize_attribute_image_values.image_format}
                            labelName={"Allowed Image Formats"}
                            validation={true}
                            onChange={(e) => { this.handleImageFieldChange(e, "image_format") }}
                        />
                    </div>
                </div>
            </div>
        </React.Fragment>
    }
}
function mapStateToProps(state) {
    return {
        attributeValues: state.attributeValues,
    };
}
export default connect(mapStateToProps)(Image);