import React, { Component } from 'react'
export default function Popup(props) {
  const cancel = e => {
    typeof props.cancel === "function" && props.cancel()
  }
  const submit = e => {
    typeof props.submit === "function" && props.submit()
    cancel()
  }
  return (
    <div className="promize-modal-cover-delete-popup">
      <div className="promize-modal modal-sm">
        <div className="promize-modal-header">
          <h3 className="modal-title">
            <span>{props.title || "Pop Up"}</span>
          </h3>
        </div>
        <div className="promize-modal-body">
          <div className="promize-form-group mt-10">
            {props.content && <p>{props.content}</p>}
          </div>
        </div>
        <div className="promize-modal-footer df df-end">
          <button className="btn btn-promize-secondary" id="cancel" onClick={(e) => cancel(e)}>{props.cancelButton || "Cancel"}</button>
          <button className="btn btn-promize-primary" onClick={(e) => submit(e)}>{props.submitButton || "Submit"}</button>
        </div>
      </div>
    </div>
  );
}
